
import XHeader from 'vux/src/components/x-header/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XDialog from 'vux/src/components/x-dialog/index.vue'

import Wechat from './../utils/wechat'
import {santaCard, santaWish} from '../config/constant'
import { pigYearPredictions } from '../config/variables'
import {santaPicture as santa, frontendUrl, activityOssPrefix } from '../config/index'
import santaApi from '../store/apis/santaWish'
import {mapState, mapActions, mapMutations} from 'vuex'
import myInfoMixin from '../mixins/myInfoMixin.js'
import { local } from '../utils/storage'

export default {
components: {
XHeader,
XDialog
},
mixins: [myInfoMixin],
directives: {
TransferDom
},
data () {
return {
activityOssPrefix,
results: pigYearPredictions,
result: '',
shareDialog: false,
cardUrl: '',
santa: santa,
santaWish: santaWish,
cardImage: document.createElement('img'),
canvas: document.createElement('canvas'),
friendList: [],
hasShared: false,
isSelf: false,
showTip: false,
showHasSharedTip: false,
myIndex: '',
userId: '',
userCoupon: ''
}
},
created () {
this.result = local.get(local.KEY_ACTIVITY_SELECTION)
const { userId } = this.$route.params
const time = new Date().getTime()
this.cardUrl = santa + santaCard[this.result] + `?time=${time}`
this.userId = userId
const validateId = this.myId ? this.myId : local.get(local.KEY_USER_ID)
if (validateId) {
if (userId == validateId) {
this.isSelf = true
} else {
this.isSelf = true
this.setShareOrigin(userId)
}
}
},
mounted () {
const result = this.results[this.result]
this.getMyGift()
Wechat.initSantaShare(this.result, `年终总结：${result.title}`, this.toGettingGift)
},
computed: {
...mapState({
shareGift: state => state.santaWish.gift,
myId: state => state.user.myInfo.id,
myInfo: state => state.user.myInfo
})
},
watch: {
shareGift: function (val, oldVal) {
if (val.length && !this.hasShared) {
this.hasShared = true
}
},
myId: function (val, oldVal) {
if (val) {
if (this.userId == this.myId) {
this.isSelf = true
} else {
this.isSelf = false
this.setShareOrigin(this.userId)
}
}
}
},
methods: {
...mapActions({
getGift: 'getGift',
getMyGift: 'getMyGift'
}),
...mapMutations({
setShareOrigin: 'setShareOrigin'
}),
toGettingGift () {
if (this.shareGift.length) {
this.$vux.toast.show({
text: '您已分享获得大礼包了',
type: 'text'
})
} else {
this.getGift({
type: this.result,
callback: (res) => {
this.shareDialog = true
this.hasShared = true
this.showTip = false
}
});
}
},
popHasSharedTip () {
this.showHasSharedTip = true
},
closeHasSharedTip () {
this.showHasSharedTip = false
},
openTip () {
if (this.hasShared) {
this.popHasSharedTip()
} else {
this.showTip = true
}
},
closeTip () {
this.showTip = false
},
closeDialog () {
this.shareDialog = false
},
goHome () {
this.$router.push({path: '/lottery'})
},
gotoWish () {
this.$router.push({path: '/santaWish'});
},
goCoupon () {
if (!this.myInfo.phone) {
this.$router.push({path: '/bindMobile', query: {type: 2}});
} else {
this.$router.push('/coupons')
}
}
}
}
